
<template>
  <div class="nav">
    <div class="main">
      <a class="item" v-on:click="prices">Prices</a>
      <a class="item" v-on:click="plant">Plant</a>
      <a class="item" v-on:click="auction">Auction</a>
    </div>
    <div class="right">
      <a class="item" v-on:click="profile" v-if="user">Profile</a>
      <amplify-sign-out v-if="user"></amplify-sign-out>
    </div>
  </div>
</template>

<!--
      <a class="item" v-on:click="products">Products</a>
      <a class="item" v-on:click="referral">Referral</a>
-->

<script>
import { Auth, Hub } from 'aws-amplify';
import AmplifyStore from '../store';


export default {
  name: 'Menu',
  data () {
    return {}
  },
  computed: {
    user() { 
      return AmplifyStore.state.user
    }
  },
  methods: {
    prices: function() {
        this.$router.push('/')
    },
    plant: function() {
        this.$router.push('/plant')
    },
    auction: function() {
        this.$router.push('/auction')
    },
    products: function() {
        this.$router.push('/products')
    },
    referral: function() {
        this.$router.push('/referral')
    },
    profile: function() {
        this.$router.push('/profile')
    },
    signOut: function() {
        this.$router.push('/auth/signOut')
    }
  }
}
</script>




