<template>
  <div class="container shifted"><center>
    <h2 class="h2">
      Buy this herb now 
    </h2>
    <table>
      <tr><td>Price: <input v-model="herbPrice" placeholder="Herb price"></td></tr>
      <tr><td colspan="2" align="center"><button class="block" v-on:click="buy">Buy now</button></td></tr>
    </table>
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf } from '../util/funcs.js';

const uuidv4 = require('uuid/v4');

export default {
  name: 'Buy',

  data () {
    return {
      herbId: null,
      herbPrice: 0,
      askAccId: null 
    }
  },

  async created() {
    this.herbId = this.$route.query.herbId;
    this.herbPrice = this.$route.query.askPrice;
    this.askAccId = this.$route.query.askAccId;
  },

  methods: {
    async buy() {
      const currentUser = await this.$Amplify.Auth.currentUserInfo();
      if (currentUser.id == this.askAccId) {
        alert('You can not buy your plant');
        return;
      }

      API.post('rpv', '/herbNewBid', {'body': {'ApiVer': 0, 'BidId': uuidv4(), 'HerbId': this.herbId, 'Bid': this.herbPrice, 'BAccName':currentUser.username}  }).then(response => {
        this.$router.push('/Plant');
      }).catch(error => {
        alert('something went wrong');
        console.log(error)
      });
    }
  }
}
</script>



