<template>
  <div>
  <h1>Top herb estimated prices</h1>
  <table width="90%" BORDER=10 BORDERCOLOR="#0000FF" BORDERCOLORLIGHT="#33CCFF" BORDERCOLORDARK="#0000CC"><tbody>
    <tr>
      <td>Herb Id</td>
      <td>Estimated price</td>
      <td>Planted at</td>
    </tr>
    <tr v-for="row in rows">
      <td class="herbid">
         {{row.HerbId}}
      </td>
      <td class="price">
        {{priceOf(row.CreateAt)}}
      </td>
      <td class="time">
        {{timeAgo(row.CreateAt)}}
      </td>
    </tr>
  </tbody></table>
  </div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf } from '../util/funcs.js';


export default {
  name: 'Home',
  async created() {
    const currentUser = this.$Amplify.Auth.currentUserInfo();
    console.log('call herbListHerbs');
    API.post('rpv', '/herbListHerbs', {'body': {'ApiVer': 0}}).then(response => {
      console.log('herbListHerbs return : %o',  response.Herbs);
      this.rows = response.Herbs;
    }).catch(error => {
      console.log(error);
    });
  },

  data () {
    return {
      rows: [ ]
    }
  },

  methods: {
    timeAgo(date) {
      return timeAgo(date);
    },
    priceOf(createAt) {
      return priceOf(createAt);
    }
  }
}
</script>



