<template>
  <div>
    <div v-if="rows.length != 0">
    <table width="90%" BORDER=10 BORDERCOLOR="#0000FF" BORDERCOLORLIGHT="#33CCFF" BORDERCOLORDARK="#0000CC"><tbody>
      <tr>
        <td>Herb Id</td>
        <td>Ask price</td>
        <td>Planted at</td>
      </tr>
      <tr v-for="row in rows">
        <td class="herbid">
           {{row.HerbId}}
        </td>
        <td class="price">
          {{row.Ask}}
        </td>
        <td class="time">
          {{timeAgo(row.CreateAt)}}
        </td>
        <td class="ask">
          <button v-on:click="buy(row)">Buy</button>
        </td>
      </tr>
    </tbody></table>
    </div>
    <h1 v-if="rows.length == 0">
    No herbs are selling
    </h1>
  </div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo } from '../util/funcs.js';

export default {
  name: 'Auction',

  data () {
    return {
      rows: [ ]
    }
  },

  async created() {
    const currentUser = this.$Amplify.Auth.currentUserInfo();
    console.log('call herbListHerbs');
    API.post('rpv', '/herbListAsks', {'body': {'ApiVer': 0}}).then(response => {
      console.log('herbMyHerbs return : %o',  response.Asks);
      this.rows = response.Asks;
    }).catch(error => {
      console.log(error);
    });
  },


  methods: {
    buy(ask) {
     this.$router.push({path: '/buy', query:{'herbId': ask.HerbId, 'askPrice': ask.Ask, 'askAccId': ask.AccId }});
    },

    timeAgo(date) {
      return timeAgo(date);
    }
  }
}
</script>



